
import { defineComponent, computed, getCurrentInstance, ComponentInternalInstance } from "vue";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
import { formatNumTwo } from "../../utils/common";

const orderHandler = (app: ComponentInternalInstance | null) => {
  const store = useStore();
  const router = useRouter();
  const route = useRoute();
  const shopId = route.query.shopId;

  // 获取当前店铺下的信息
  const shopList = computed(() => {
    let shop = [];
    typeof shopId === "string" ? shop.push(store.state.cartList?.[shopId]) : (shop = []);
    return shop;
  });

  // 计算实付金额
  const total = computed(() => {
    let total = 0;
    shopList.value.forEach((item) => {
      const products = item.productList;
      for (const key in products) {
        const product = products[key];
        total += product.currentPrice * product.count;
      }
    });
    return formatNumTwo(total);
  });

  // 返回上一页
  const handleClickBack = () => {
    router.back();
  };

  const handleSubmitOrder = () => {
    app?.appContext.config.globalProperties.$confirm({
      title: "确认要离开收银台？",
      content: "确认要离开收银台？",
      cancelText: "取消订单",
      okText: "确认支付",
      ok: () => {
        console.log("ok");
      },
      cancel: () => {
        console.log("cancel");
      },
    });
  };

  return { shopList, total, handleClickBack, handleSubmitOrder };
};

export default defineComponent({
  setup() {
    const app = getCurrentInstance();

    const { shopList, total, handleClickBack, handleSubmitOrder } = orderHandler(app);

    return { shopList, total, handleClickBack, handleSubmitOrder };
  },
});
